import React from 'react'
import {FlexWrapper} from '../../../components/FlexWrapper'
import aboutPhoto from '../../../assets/images/about_me_photo.jpg'
import {Container} from '../../../components/Container'
import {Button} from '../../../components/Button'
import {AboutMyBG, background} from './aboutMyBG/AboutMyBG'
import {S} from './About_Styles'


const aboutText: string = 'Frontend-    IT-,           .    SPA   React, NextJS, TypeScript, Redux, RTK, RTK Query.     CSS, SCSS, MUI  Style-Components.   ,       .               .'

const expData: Array<background> = [
    {
        text: 'Front-end development: React, Redux, TypeScript, NextJS, Storybook',
        place: 'IT-Incubator:',
        date: '2023 - ..'
    }
]

const eduData: Array<background> = [
    {
        text: ' ',
        place: ' :',
        date: '2012 - 2018'
    }
]

export const About: React.FC = () => {
    return (
        <S.About id={'about'}>
            <Container>
                <FlexWrapper align={'center'} justify={'space-between'} gap={'25px'}>

                    <S.AboutPhoto src={aboutPhoto} alt={'MyPhotoWithFlowers'}/>

                    <S.AboutTextArea>
                        <S.SectionAboutTitle> </S.SectionAboutTitle>
                        <S.AboutText>{aboutText}</S.AboutText>

                        <S.SectionAboutTitle></S.SectionAboutTitle>
                        <AboutMyBG infoAboutMe={expData}/>

                        <S.SectionAboutTitle></S.SectionAboutTitle>
                        <AboutMyBG infoAboutMe={eduData}/>

                        <Button> CV</Button>
                    </S.AboutTextArea>
                </FlexWrapper>
            </Container>
        </S.About>
    )
}





